module.exports = {"patrons":[{"image":"patrons/microsoft.png","title":"Microsoft","link":"microsoft.com","url":"https://microsoft.com","content":"마이크로소프트의 목표와 가치는 전세계의 사람과 기업이 잠재력을 최대한 발휘할 수 있도록 돕는 것입니다.\nAzure, Office 365, Windows 10, 서피스 등 다양한 소프트웨어, 하드웨어를 통하여 새로운 가치를 전달합니다."},{"image":"patrons/jetbrains.png","title":"단군소프트","link":"jetbrains.com","url":"http://jetbrains.com","content":"JetBrains는 지능형 IDE 및 협업툴을 개발하는 소프트웨어 개발사 이며, 프라하(체코)에 본사를 두고, 러시아, 독일 및 미국에 지사를 운영하고 있습니다. \nJetBrains의 800명 이상의 직원들은 JAVA 생산성 향상 IDE인 IntelliJ IDEA 및 .NET 개발툴 ReSharper를 주력으로 한국 시장을 공략하고 있으며 포춘 500대 기업 중 431개 기업이 JetBrains의 IDE를 사용하고 있습니다.\nIT 업계는 시장 경쟁이 치열하며, 발빠른 시장 선점이 성공 비결의 핵심입니다. JetBrains 의 제품들은 프로그래머들이 불필요한 시간을 낭비하지 않도록 하여 기업의 생산성을 높이는데 획기적인 도움을 주고 있습니다."},{"image":"patrons/elastic.png","title":"Elastic","link":"www.elastic.co","url":"https://www.elastic.co","content":"2012 년에 설립 된 Elastic은 실시간 검색, 로깅, 보안 및 분석에 사용되는 오픈 소스 Elastic Stack (Elasticsearch, Kibana, Beats 및 Logstash), X-Pack (상용 기능 플러그인) 및 Elastic Cloud (SaaS 제품)를 개발하고 있으며, 현재까지  2억건에 가까운 누적 다운로드를 기록하고 있습니다. 전 세계 Elastic 커뮤니티에 85,000 명의 회원들이 있으며, Cisco, eBay, Goldman Sachs, NASA, Microsoft, Mayo Clinic, The New York Times, Wikipedia 및 Verizon을 포함한 수천 개의 조직이 Elastic을 사용하여 핵심 업무 시스템을 구축하였습니다. Benchmark Capital, Index Ventures 및 NEA로 부터 1 억 달러 이상의 자금을 투자받았으며, Elastic에는 30 여개국에 700명이 넘는 직원들을 두고 있습니다."},{"image":"patrons/grapecity.png","title":"그레이프시티(주)","link":"grapecity.co.kr","url":"http://www.grapecit.co.kr","content":"그레이프시티는 전 세계 수 만명의 사용자를 보유한 세계 최대의 글로벌 UI 컴포넌트 개발사입니다.\nSpread(엑셀), SpreadJS(자바스크립트 엑셀), ComponentOne(종합 UI툴), Wijmo(자바스크립트 차트&그리드), ActiveReports(리포팅)등  다양한 솔루션을 통해 개발자들이 쉽고 빠르게 UI 개발을 할 수 있도록 돕고 있습니다. 특히, 다양한 기능, 윈폼-웹-모바일을 모두 아우르는 폭넓은 플랫폼, 감각적인 디자인, 압도적인 퍼포먼스는 그레이프시티의 큰 강점으로 꼽히고 있습니다.\n저희 그레이프시티 코리아는 동종 외산 개발툴 업계유일의 한국 지사를 통한 기술지원 서비스, 제품의 한글화, 커뮤니티 운영, 한글 데모 제공 등 다양한 방법을 통하여 한국 고객들을 위한 서비스 제공에 최선의 노력을 다하고 있습니다."},{"image":"patrons/mysql.png","title":"한국 오라클 유한회사","link":"oracle.com","url":"http://www.oracle.co.kr","content":"한국 오라클은 1989년에 설립되었으며, 데이터베이스를 근간으로 하는 테크놀로지, 미들웨어, 애플리케이션, 하드웨어 시스템의 4가지 비즈니스 영역의 솔루션 공급뿐 아니라 클라우드 서비서, 컨설팅 및 서포트 서비스를 제공하면서 국내 IT 시장의 발전과 고객 기업의 비즈니스 성공에 기여하고 있습니다. MySQL은 전 세계 가장 널리 보급된 오픈소스 데이터베이스로서 현재 오라클 MySQL GBU(Global Business Unit)에서 운영되고 있습니다.\nwww.mysql.com\n한국 MySQL 사용자 그룹은 MySQL 정보를  공유하기 위해 만들어진 공간입니다. 국내에서 MySQL을 사용하시고 고민하시는 분들과 함께 질문과 답변을 할 수 있는 곳입니다.\n커뮤니티 주소: https://www.facebook.com/groups/623067261102382"},{"image":"patrons/drimaes.png","title":"(주)드림에이스","link":"drimaes.com","url":"http://www.drimaes.com","content":"(주)드림에이스(DrimAES)는 리눅스 재단에서 차량용 임베디드 리눅스 표준 플랫폼으로 공개한 AGL(Automotive Grade Linux) 과 차량용 임베디드 소프트웨어 표준 플랫폼인 AUTOSAR 전문기업입니다. 차량 반도체 1위 기업인 독일의 Infineon 의 창업경진대회에서 전 세계 2위에 힘입어 대구경북과학기술원(DGIST)에서 투자한 연구기업으로, 국내 AP SoC 전문기업 넥셀(NEXELL)의 솔루션 파트너로 현재 차량용 인포테인먼트 및 3D 기반의 AVM(Around View Monitoring) 솔루션을 개발하고 있습니다"},{"image":"patrons/cubrid.jpg","title":"큐브리드","link":"cubrid.com","url":"http://www.cubrid.com","content":"CUBRID는 관계형 DBMS 제품으로 엔터프라이즈 시장에서 요구하는 대용량 데이터 처리 능력 및 성능, 안정성, 가용성, 관리 편의성을 제공하고 있습니다. 2008년 소스코드 공개 이후 국내외 30만건 이상의 제품 다운로드 실적을 보유하고 있으며, 국가정보자원관리원, 국방통합데이터센터,  네이버 등 600개 이상의 시스템에서 3,000여개 DB 인스턴스가 서비스 중에 있습니다."},{"image":"patrons/sqlgate.png","title":"주식회사 체커","link":"SQLGate.com","url":"http://sqlgate.com","content":"체커는 기업의 데이터 업무 생산성을 극대화 하자라는 비전으로 대한민국 최고의 전문가가 사용하는 가장 혁신적인 데이터베이스 통합 개발 도구, SQLGate를 만드는 개발사입니다. SQLGate는 Oracle, SQL Server, PostgreSQL, MySQL 등 세계 TOP DBMS 6종을 관리/개발할 수 있도록 지원하고 있으며 2003년 출시 이후 국내 2,500곳 이상의 중견/대기업과 3,000곳 이상의 중소 IT회사가 사용하고 있는 대한민국 최고의 데이터 생산성 도구 입니다."},{"image":"patrons/festa.png","title":"Festa","link":"festa.io","url":"https://festa.io","content":"Festa는 손쉽게 이벤트를 주최하고 등록할 수 있는 공간입니다. 복잡한 결제 시스템과 참가자 관리의 어려움을 해소하여 주최자와 참가자가 시간 낭비 없이 만날 수 있도록 돕고 있습니다. 국내 개발자 커뮤니티 모임이 성공적으로 진행될 수 있도록 적극 지원하고 있습니다."},{"image":"patrons/hanbit.png","title":"한빛미디어","link":"hanbit.co.kr","url":"http://www.hanbit.co.kr","content":"‘책으로 여는 IT 세상’을 꿈꿉니다.\n한빛미디어는 IT 세상의 주역들을 위한 프로그래밍, 컴퓨터공학, IT 에세이, 리얼타임(전자책), OA, 그래픽, 나와 내 아이를 위한 실용 등 다양한 분야의 책을 만듭니다."},{"image":"patrons/jpub.jpg","title":"제이펍출판사","link":"jpub.kr","url":"http://www.jpub.kr","content":"2009년 4월에 [서버/인프라를 지탱하는 기술]을 시작으로2018년 1월 현재까지 215종의 IT 관련 서적을 펴낸 IT 서적 전문 출판사입니다.\n앞으로도 IT 업계에서 일하는 분들에게 필요한 서적을 제때, 제대로 만들기 위해 최선을 다하겠습니다."},{"image":"patrons/jiandson.png","title":"지앤선출판사","link":"jiandson.co.kr","url":"http://www.jiandson.co.kr","content":"대한민국 모든 IT 개발자의 동반자를 꿈꿉니다. 정말 필요한 도서라면 단 한 사람의 독자를 위해서도 출간할 수 있는 사명감을 가진 출판사!"},{"image":"patrons/gilbut.png","title":"(주)도서출판 길벗","link":"gilbut.co.kr","url":"http://www.gilbut.co.kr","content":"독자의 1초를 아껴주는 정성!\n세상이 아무리 바쁘게 돌아가더라도 책까지 아무렇게나 빨리 만들 수는 없습니다.\n인스턴트 식품 같은 책보다는 오래 익힌 술이나 장맛이 밴 책을 만들고 싶습니다.\n땀 흘리며 일하는 당신을 위해 한 권 한 권 마음을 다해 만들겠습니다.\n마지막 페이지에서 만날 새로운 당신을 위해 더 나은 길을 준비하겠습니다.\n독자의 1초를 아껴주는 정성을 만나보십시오."}]}

