module.exports = {"faqs":[{"title":"Workshop 참가를 원할 경우 어떻게 해야 하나요?","content":"spicalong@gmail.com 으로 참가하고자 하는 Workshop을 작성하시어 신청을 별도로 하여 주시기 바랍니다. 확인 후 별도로 준비사항을 공지할 예정입니다."},{"title":"참관비는 어떻게 납부 해야 하나요?","content":"참관비는 Festa 사이트에서 신용카드로 결제 가능합니다."},{"title":"세금계산서 발행이 가능한가요?","content":"먼저 카드 결제 후 help@festa.io로 연락 바랍니다. 절차를 안내해드린 후 세금계산서를 발행 해드리겠습니다."},{"title":"참가확인증 발급은 어떻게 되나요?","content":"참가확인증은 행사 당일 현장에서 접수합니다. 미처 발급을 못 받으신 경우에는 아래의 메일로 연락 주시기 바랍니다. spicalong@gmail.com Tel: 017-277-0917"},{"title":"법인 카드로 결제 시 어떻게 해야 하나요?","content":"Festa에서 법인 카드로 결제 가능합니다."},{"title":"행사당일 주차지원은 되나요?","content":"주차권은 제공되지 않으니, 가능하면 대중교통수단 이용을 부탁드립니다. 주말의 경우 근처 연합뉴스 건물에 하루종일 5천원에 주차가 가능합니다."},{"title":"점식식사는 제공이 되나요?","content":"참가신청 하신 분들 중 행사 당일 선착순 200분에게 점심식사를 제공합니다."},{"title":"사전 등록자 대신, 다른 사람이 참석해도 되나요?","content":"네. 등록하신 분의 명함을 지참하시고, 대리 참석하셔도 무방합니다."},{"title":"기타 질문사항이 있다면 어디에 문의하나요?","content":"spicalong@gmail.com 으로 메일 주시거나 또는 017-277-0917으로 연락 주시면 빠르게 답변드리도록 하겠습니다."}]}

