module.exports = {"communities":[{"image":"community/codeigniter.gif","title":"Codeigniter 한국사용자포럼","link":"cikorea.net","url":"http://cikorea.net","content":"PHP 프레임워크인 CodeIgniter의 매뉴얼 번역 및 개발 소스 공유, 커뮤니케이션을 목적으로 2009년 개설되어 꾸준히 활동하고 있습니다. 온라인 활동뿐만 아니라 서울, 부산, 광주에서 매달 Offline 정모를 진행하고 있으며 개발자 스터디 지원, 각종 컨퍼런스에서 초급/심화 교육을 진행하고 있습니다."},{"image":"community/mongodb-korea.jpg","title":"MongoDB Korea","link":"krmug","url":"https://facebook.com/groups/krmug/","content":"MongoDB Korea는 MongoDB를 사용하는 사람들의 커뮤니티입니다. 2013년 7월에 커뮤니티가 시작되었으며, 현재 1,700여 명의 회원이 활동 중입니다. 정형화된 틀에서의 논의보다는 다양한 방식과 주제로 많은 정보를 공유하고 있습니다. 자유로운 질문, 토론, 답변, 논쟁, 정보공유와 오프라인 모임을 지향하고 있으며, MongoDB 뿐만 아니라 관련된 다양한 노하우를 나눠주실 분의 오프라인 모임 제안도 환영합니다."},{"image":"community/okky.jpg","title":"OKKY","link":"okky.kr","url":"https://okky.kr","content":"삶의 진솔한 얘기와 기술이 함께하는 커뮤니티, 개발자를 행복하게 해주는 커뮤니티, 16년의 개발정보가 녹아있는 사이트, OKKY 입니다."},{"image":"community/osxdev.jpg","title":"OSXDev","link":"osxdev.org","url":"http://osxdev.org","content":"맥/iOS 개발자 커뮤니티는 2001년도 국내 최초 맥 개발자 커뮤니티로 시작해서, OSX 개발 기술을 스터디하고 애플 기술 문서 한글화 작업등을 진행해왔다. 최근 iOS 개발자들이 많아지면서 자연스럽게 국내 최고의 맥/iOS 개발자 커뮤니티로 거듭나고 있다. 매년 맥 OSX와 iOS 개발 스터디 그룹 활동과 정기 세미나를 개최하고 있으며, 최근 맥/iOS 개발 자료를 번역하거나 책으로 소개하는 프로젝트도 진행하고 있다."},{"image":"community/taeyo.gif","title":"Taeyo.NET","link":"taeyo.net","url":"www.taeyo.net","content":"Taeyo.NET 1998년부터 운영되어 왔으며 8만명 이상의 회원수와 1억 이상의 뷰카운트를 자랑하는 국내 최장수 ASP/ASP.NET커뮤니티입니다. 국제닷넷협회의 회원 사이트이며, 대표 운영자인 Taeyo는 한국인 최초로 Microsoft MVP Insider를 수상하였습니다. 지금까지 20명 이상의 Microsoft MVP를 배출하였으며, 한국의 100대 사이트에 선정되기도 하였습니다."},{"image":"community/django-girls.jpg","title":"장고걸스 서울","link":"djangogirls.org/seoul","url":"http://djangogirls.org/seoul","content":"장고걸스는 여성들의 프로그래밍 학습의 기회를 제공하고 지지해주고자 설립된 국제 비영리 단체입니다. 2014년 7월, 올라 시타스카(Ola Sitarska)와 올라 센데카(Ola Sendecka) 두 폴란드 여성들에 의해 베를린에서 시작하였으며 STEM 분야에 상대적으로 소수인 여성분들의 진출을 도와 Tech 분야의 다양성을  높이고자 노력하고 있습니다."},{"image":"community/keras-korea.jpg","title":"케라스 코리아(Keras Korea)","link":"KerasKorea","url":"https://www.facebook.com/groups/KerasKorea/","content":"케라스 코리아 그룹은 파이썬으로 구현된 쉽고 간결한 딥러닝 라이브러리인 케라스(Keras)에 대한 이야기를 나누는 공간입니다. 그룹에서는 각 분야의 전문가분들이 딥러닝 기술을 쉽게 접목하거나 딥러닝 기술에 대해 입문하시고 싶은 분들에게 도움을 드리고 소통하는 것에 목표를 두고 있습니다. 또한 분야별로 어떤 문제에 어떻게 딥러닝을 접목하려는 지 또는 해본 경험을 토론하고자 분야별 모임(#케코x분야)을 두고 있습니다."},{"image":"community/iot-labs.png","title":"IoT Labs","link":"IoTLabs","url":"https://www.facebook.com/groups/IoTLabs","content":"IoT Labs 는 IoT 와 Web 기술을 접목하여 손쉬운 사용을 돕는 개발자 커뮤니티 입니다. 현재는 IoT Labs - Farm 을 주력으로 개발하고 있으며, 농업 생산 환경을 측정하여 시각화 하는 작업을 진행 하고 있습니다. Github 을 통해 개발하며 2~3주 간격으로 오프 모임도 병행하고 있습니다. "},{"image":"community/cpp.png","title":"C++ Korea","link":"cppkorea","url":"https://www.facebook.com/groups/cppkorea","content":"C++ Korea는 2013년 11월에 설립된 그룹으로, 한국 C++ 사용자들에게 최신 C++ 뉴스 및 관련 자료를 제공하고 있습니다. 현재 약 3,800명의 회원이 있으며, 여러차례 스터디와 세미나를 진행했습니다. 현재는 C++ 템플릿 스터디, C++ 핵심 가이드라인 한글화 프로젝트를 진행하고 있으며, 하반기를 목표로 C++ 컨퍼런스를 준비 중에 있습니다."},{"image":"community/ASP.NET.jpg","title":"ASP.NET Korea","link":"aspnet.kr","url":"http://aspnet.kr","content":"한국 ASP.NET 개발자 모임입니다. ASP.NET에 관심이 있다면 누구나 참여하실 수 있는 공개 그룹으로 행복한 개발자를 꿈꾸는 분들이라면 누구나 환영합니다."},{"image":"community/openstack-user-group.png","title":"오픈스택 한국 커뮤니티","link":"openstack.or.kr","url":"http://openstack.or.kr","content":"오픈스택 (OpenStack)은 클라우드 관리 플랫폼을 구축하기 위한 오픈 소스 소프트웨어 프로젝트로, 개별적으로 설치하는 6개의 코어 및 19개의 선택 컴포넌트들을 서로 유기적으로 연동하여 직접 클라우드를 구성할 수 있습니다. 2010년 7월 Rackspace사와 NASA로 부터 시작한 이후, 전 세계 179개 국가로부터 50,000명이 넘는 커뮤니티 회원이 참여 및 기여를 통해 (2016년 7월 통계) 꾸준히 성장하고 있습니다. 오픈스택 한국 커뮤니티는 공식 사용자 그룹으로서 다양한 정기 세미나, 스터디 활동, 번역팀 활동을 비롯하여 페이스북, 메일링리스트, IRC, Slack 등 여러 커뮤니케이션 채널을 통해 많은 오픈스택 유저 및 관심있는 분들과 활발히 활동하고 있습니다."},{"image":"community/ubuntu.gif","title":"우분투 한국커뮤니티","link":"ubuntu-kr.org","url":"http://ubuntu-kr.org","content":"2005년부터 시작된 우분투한국커뮤니티는 국제적으로 인증된 한국 공식 로컬팀이며, 우분투를 사용하거나 번역 및 개발에 기여하는 사람들로 구성되어 있습니다. 정기 세미나, 분기별 그룹스터디, 프로젝트, 워크샵 등 행사를 통해 오프라인 상에서의 회원간 교류를 도모하고 있습니다. 또한 비 수도권 지역 지역팀 개설과 해외 커뮤니티와의 교류를 통해 활동 영역을 더 넓히고 있습니다. 다양한 소통과 질문이 가능하도록 포럼, 페이스북 그룹, 위키, Ask Ubuntu KR, Slack, IRC 등을 운영하고 있습니다."},{"image":"community/jsdevkr.png","title":"자바스크립트 개발자 포럼","link":"jsdev.kr","url":"https://jsdev.kr","content":"자바스크립트 개발자 포럼은 J-vaScript, TypeScript, Parse, React, Vue, AxisJ, Titanium 등 다양한 주제로 포럼을 운영하고 있습니다. 자바스크립트에 관심 있는 개발자라면 누구나 참여 가능합니다."},{"image":"community/spark.jpg","title":"스파크 사용자 모임","link":"sparkkoreauser","url":"https://www.facebook.com/groups/sparkkoreauser","content":"스사모 (한국 스파크 사용자 모임, Apache Spark Korea User Group) 는 빠르고 간편한 인메모리 대용량 데이터 분석 엔진인 스파크(Apache Spark) 한국 사용자 모임입니다. Spark, Spark SQL, Spark Streaming, MLLib (Machine Learning), Zeppelin 등 다양한 주제에 대한 토론을 나눕니다. \n4600명가 의 회원이 있고 작게는 10명 내외의 스터디부터 300명 이상의 대규모 행사도 진행했습니다."},{"image":"community/slipp.png","title":"SLiPP","link":"slipp.net","url":"http://slipp.net","content":"SLiPP 커뮤니티는 자바 웹 백엔드를 주제로 오프라인 스터디를 시작했다. \n현재는 자바 웹 백엔드에서 범위를 넓혀 웹 프론트엔드, 개발자 영어, 알고리즘, 다양한 JVM 언어등 다양한 주제로 활발하게 스터디를 운영하고 있다.\n다양한 개발자들이 서로 소통하고 같이 학습하면서 성장하는 것을 지향하고 있다."},{"image":"community/kaggle-break.png","title":"캐글뽀개기","link":"kagglebreak","url":"https://www.facebook.com/groups/kagglebreak","content":"캐글(kaggle)은 데이터 과학에 적합한 모델을 만들기 위해 예측 모델링 및 분석 경쟁을 위한 플랫폼입니다. 캐글뽀개기는 캐글 대회를 라면처럼 뽀개는 것을 표로 분석 기술을 연구 및 공부하는 모임입니다. 실습형 데이터 분석 주제로 주기적인 스터디를 진행하며 머신러닝을 뽀개기 가장 쉬운 방법을 커뮤니티에서 배울 수 있습니다. 회사, 학교, 공공기관이 아닌 스터디 그룹에서 계급/학벌과 관계없이 누구나 자유롭게 공부할 수 놀이터입니다."},{"image":"community/babel-pish.png","title":"바벨피쉬","link":"babelPish","url":"https://www.facebook.com/groups/babelPish","content":"친구, 혹은 비서에게 말하듯이 내가 한 말을 이해하는 컴퓨터 시스템이 있다면 얼마나 편리할까요? 이는 더이상 SF 영화 속에서나 가능한 일이 아닙니다. 저희 바벨피쉬는‘자연어처리’를 통한 인공지능 시스템 구현을 목표로 주기적으로 스터디를 진행하는 커뮤니티입니다."},{"image":"community/oss.png","title":"OSS개발자포럼","link":"ossdevforum","url":"https://www.facebook.com/groups/ossdevforum","content":"OSS개발자포럼은 개발자에게 유용한 정보(기술, 행사 등)를 공유하고 오프라인 행사(교육 및 멘토링)를 통해 선후배 개발자 간 네트워킹을 하며 정보를 교류하는 온라인 커뮤니티입니다."},{"image":"community/lascala.jpg","title":"라스칼라코딩단","link":"scala-korea","url":"https://groups.google.com/forum/#!forum/scala-korea","content":"라스칼라 코딩단은 스칼라 프로그래밍 언어를 중심으로 구성된 커뮤니티입니다. 스칼라는 JVM 위에서 작동하며 기존의 자바 라이브러리를 그대로 활용할 수 있다는 장점이 있습니다. 동시에 함수형 프로그래밍의 패러다임을 수용하고 있습니다. 스칼라는 그 이름의 유래답게 동시성, 병렬 처리부터, DSL을 통한 스크립트 언어에서 엔터프라이즈 제품까지 모두 아우르는 언어입니다. 라 스칼라 코딩단에서는 일방적인 발표나 이론보다, 코드를 놓고 타이핑하는 실전 위주의 모임을 갖고 있습니다. 그렇다고 초보가 접하기 어려운 것은 아닙니다. 모든 것은 코드를 보며 사소한 부분부터 이야기할 수 있으니까요."},{"image":"community/java-cafe.png","title":"자바카페","link":"javacafe","url":"https://www.facebook.com/groups/javacafe","content":"자바카페는 오프라인 스터디 모임입니다. 매년 상반기 하반기로 나눠서 스터디 모집을 하고 있으며, 스터디 모집 외에도 마소 기고, 세미나 등의 개발자 역량 향상을 위한 활동을 진행하고 있습니다. 개발자의 능력 향상뿐만 아니라 개발자 사이의 오프라인 모임을 주선하여 좀 더 좋은 개발자들 간의 관계를 조성하기 위해서 노력하는 커뮤니티입니다."},{"image":"community/efl.jpg","title":"EFL 한국 커뮤니티","link":"efl.or.kr","url":"http://efl.or.kr","content":"EFL (Enlightenment Foundation Libraries) 프로젝트는 GUI 툴킷 라이브러리로 애플리케이션 개발에 필요한 기능을 제공한다. 타이젠 핵심 UIFW에 해당하며 타 GUI 툴킷 라이브러리 대비 가벼우면서도 좋은 성능을 보장한다는 이점을 지닌다. EFL 한국 커뮤니티는 EFL을 국내에 알리고 자료 제공 및 세미나 등을 통해 사용자들에게 EFL 정보를 제공하는 데 노력하고 있다"},{"image":"community/korea-azure-group.png","title":"Korea Azure User Group (KRAZURE)","link":"www.krazure.com","url":"www.krazure.com","content":"Azure를 사용하는 한국 사용자들의 모임으로써, Azure 사용자만이 아니라 관심이 있는 모든 사용자가 가입 가능한 커뮤니티입니다. 실제 Azure를 사용하고 있는 사용자들 간의 토론의 장을 마련하고 있으며, Azure 및 관련 기술 등에 대한 자료를 공유하고 있습니다. 매월 토요일에 Saturday Azure Live 오프라인 세미나를 개최하고 있습니다."},{"image":"community/comu.png","title":"코딩이랑 무관합니다만","link":"System.out.Coding","url":"https://facebook.com/groups/System.out.Coding","content":"많은 분들이 유머페이지로 알고 계시지만 유머 페이지가 이니라 놀랍게도 개발자 커뮤니티입니다.\n•이 그룹에는 개발자들, IT에 관심있는 자들, 질문하고 싶은 자들, 닭가슴살처럼 퍽퍽한 삶에 한 조각의 치킨무와 같은 유머를 원하시는 자들 등 여러 분들이 있습니다. \n•그래서 다른 그룹에서는 하기 힘든 질문과 정보의 교류가 가능합니다. 아무리 허접한 질문이라도 친절하게 답변해주는 던전 고수들이 많습니다.\n•그래서 입문자들이 더 편하고 재미있게 기술에 입문하는데 기여하고 있습니다."},{"image":"community/jbug-korea.jpg","title":"Jboss User Group","link":"jbossusergroup","url":"https://facebook.com/groups/jbossusergroup","content":"JBoss User Group Korea(이하 JBUG)는 세계 최대의 오픈소스 그룹 중 하나인 JBoss 한국 커뮤니티입니다. JBoss 내의 프로젝트뿐만 아니라 다양한 오픈소스에 대한 정보공유, 세미나, 스터디 를 진행하고 있습니다. 매월 마지막 주 토요일 정기 세미나(무료!)가 진행되며 누구나 참여/발표할 수 있습니다."},{"image":"community/harmonycar.png","title":"하모니카 (HarmonyCar)","link":"harmonycar.org","url":"www.harmonycar.org","content":"하모니카(HarmonyCar) 커뮤니티는 차량에 들어가는 임베디드 기술들을 교류하는 차량용 임베디드 전문 커뮤니티 입니다. 차량이 점차 스마트 폰 처럼 고도화 되고 있고 이에 따라 다양한 하드웨어, 소프트웨어 기술들이 표준화되고 있는 만큼 이런 기술들에 대해 정보를 교류하고 차량 관련 기술들의 오픈 소스화를 앞장서 차량 기술의 저변화에 앞장서는 커뮤니티가 되도록 노력하겠습니다. 이와 관련하여 AUTOSAR 라는 차량 임베디드 표준 플랫폼과 리눅스 재단에서 차량용 인포테인먼트 임베디드 리눅스 표준 플랫폼으로 공개한 AGL (Automotive Grade Linux) 이라는 오픈 소스 관련 정보들을 중심으로 다양한 차량 관련 기술들을 공유할 계획입니다."},{"image":"community/tnf.gif","title":"TNF/Needlworks","link":"textcube.org","url":"http://textcube.org","content":"TNF/Needlworks 는 웹의 다양성 유지를 위한 활동과 함께 오픈소스 블로그 소프트웨어인 텍스트큐브를 만드는 오픈소스 커뮤니티입니다. 2006년부터 활동을 시작했으며 현재는 텍스트큐브 2.0 및 파생 프로젝트를 준비하고 있습니다."}]}

